#table_id {
  border-spacing: 1;
  background: white;
  border-radius: 10px;
  overflow: hidden;
  width: 100%;
  margin: 0 auto;
  position: relative;
}
#table_id * {
  position: relative;
}
#table_id td, #table_id th {
  padding-left: 8px;
}
#table_id thead tr {
  height: 60px;
  background: rgb(0, 123, 255);
  font-size: 16px;
  color: white;
}
#table_id tbody tr {
  height: 48px;
  border-bottom: 1px solid #E3F1D5;
}
#table_id tbody tr:last-child {
  border: 0;
}
#table_id td, #table_id th {
  text-align: left;
}
#table_id td.l, #table_id th.l {
  text-align: right;
}
#table_id td.c, #table_id th.c {
  text-align: center;
}
#table_id td.r, #table_id th.r {
  text-align: center;
}

@media screen and (max-width: 35.5em) {
  #table_id {
    display: block;
  }
  #table_id > *, #table_id tr, #table_id td, #table_id th {
    display: block;
  }
  #table_id thead {
    display: none;
  }
  #table_id tbody tr {
    height: auto;
    padding: 8px 0;
  }
  #table_id tbody tr td {
    padding-left: 45%;
    margin-bottom: 12px;
  }
  #table_id tbody tr td:last-child {
    margin-bottom: 0;
  }
  #table_id tbody tr td:before {
    position: absolute;
    font-weight: 700;
    width: 40%;
    left: 10px;
  }
  #table_id tbody tr td:nth-child(1):before {
    content: "#";
  }
  #table_id tbody tr td:nth-child(2):before {
    content: "Name";
  }
  #table_id tbody tr td:nth-child(3):before {
    content: "Phone";
  }
  #table_id tbody tr td:nth-child(4):before {
    content: "Email";
  }
  #table_id tbody tr td:nth-child(5):before {
    content: "Company";
  }
    #table_id tbody tr td:nth-child(6):before {
    content: "Address";
  }
}


blockquote {
  color: white;
  text-align: center;
}